.card_row {
  position: relative;
  padding-right: 30px;
}
.card_row:not(:last-child)::before {
  position: absolute;
  content: '';
  left: calc(100%/6/2 - 30px/2 - 14px/2);
  bottom: 10px;
  width: 14px;
  height: 14px;
  box-sizing: border-box;
  border: 2px solid #666;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
}
.card_row:not(:last-child)::after {
  position: absolute;
  content: '';
  left: calc(100%/6);
  bottom: 10px;
  width: calc(100%/6*5 - 30px);
  border: 1px dashed #666;
}
.card_list {
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
}
.card_list .card_row {
  display: flex;
  padding: 5px 0;
}
.card_list .card_row .card_main {
  width: calc(100%/6 - 30px);
  flex-shrink: 0;
  margin-right: 30px;
}
.card_list .card_row .card_items {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
}
.card_list .card_row .card_items .card_item,
.card_list .card_row .card_items .card_btn,
.card_list .card_row .card_items .card_add {
  width: calc(100%/(6 - 1) - 30px);
  margin-right: 30px;
  margin-bottom: 30px;
}
.card_list .card_row .card_items .card_item.dragstart,
.card_list .card_row .card_items .card_btn.dragstart,
.card_list .card_row .card_items .card_add.dragstart {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_list .card_row .card_items .card_item.dragstart {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_list .card_row.dragstart {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_main,
.card_item,
.card_btn,
.card_add {
  position: relative;
  background-color: #fff;
  box-sizing: border-box;
  height: 80px;
  user-select: none;
}
.card_item {
  display: flex;
  align-items: center;
}
.card_item .card_icon {
  width: 50px;
  height: 100%;
  flex-shrink: 0;
}
.card_item .card_content {
  display: flex;
  flex-direction: column;
  width: calc(100% - 50px);
}
.card_item .card_content .card_en_name,
.card_item .card_content .card_cn_name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card_item .icon_remove,
.card_item .icon_edit {
  position: absolute;
  right: 0;
}
.card_item .icon_remove {
  top: 0;
}
.card_item .icon_edit {
  bottom: 0;
}
.card_main {
  display: flex;
  align-items: center;
  padding-left: 20px;
}
.card_main .card_content {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.card_main .card_content .card_en_name,
.card_main .card_content .card_cn_name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card_main .icon_edit {
  position: absolute;
  right: 0;
  bottom: 0;
}
.card_add {
  outline: 2px dashed #00f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card_btn {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page_ct {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.page_ct > .page_top {
  width: 100%;
  height: 50px;
  background: #666;
}
.page_ct > .page_content {
  display: flex;
  height: calc(100% - 50px);
}
.page_left {
  height: 100%;
  width: 250px;
  flex-shrink: 0;
  transition: all .3s;
}
.page_left.collapse {
  margin-left: -250px;
  position: relative;
  z-index: 2;
}
.page_left.collapse .collapse_left {
  transform: translateX(50px);
}
.page_right {
  width: calc(100% - 250px);
  flex-grow: 1;
  background: #ccc;
}
.left_head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  background-color: #888;
  color: #f00;
  padding-left: 20px;
}
.left_head .oper_list {
  display: flex;
  align-items: center;
  padding-right: 20px;
}
.left_head .oper_list .icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.left_head .oper_list .collapse_left {
  transition: all .3s;
}
.left_list {
  height: calc(100% - 70px);
  user-select: none;
}
.left_list_item {
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding-left: 25px;
}
.left_list_item .icon {
  flex-shrink: 0;
}
.left_list_item .menu_name {
  padding-left: 15px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.right_head {
  display: flex;
  align-items: center;
  height: 70px;
  border-bottom: 1px solid #aaa;
  box-sizing: border-box;
}
.page_set {
  position: fixed;
  background-color: #fff;
  top: 10%;
  height: 80%;
  left: 10%;
  width: 80%;
}
